<template>
  <div>
    <div class="loadingView" v-if="!banners">
      <div class="loading"></div>
      <van-loading type="spinner" size="20" vertical />
    </div>

    <div class="recommend" v-else>
      <div class="banner" v-if="banners">
        <van-swipe @change="onChange" autoplay="3000">
          <van-swipe-item
            v-for="item in banners"
            :key="item.model.cover"
            class="vanSwipeItem"
          >
            <img :src="item.model.cover" alt="" />
            <div class="describe">{{ item.model.title }}</div>
          </van-swipe-item>
          <template #indicator>
            <div class="custom-indicator">
              <span>{{ current + 1 }}</span> / {{ banners.length }}
            </div>
          </template>
        </van-swipe>
      </div>

      <ul class="tuijian" v-if="MiddleCardList[0]">
        <router-link
          tag="li"
          :to="'/videoplay?id=' + item.id"
          v-for="item in MiddleCardList[0][0]"
          :key="item.id"
        >
          <div class="img">
            <img :src="item.cover" alt="" />
            <div class="vedioInfo">
              <div class="left">
                <div class="watch">
                  <span class="iconfont icon-zhiboguankanshu"></span>
                  {{ item.count.count_view | CountHandle }}
                </div>
                <div class="like">
                  <span class="iconfont icon-dianzan1"></span>
                  {{ item.count.count_like }}
                </div>
              </div>
              <div class="time">{{ item.duration | playerTime }}</div>
            </div>
          </div>
          <h3>{{ item.title }}</h3>
          <div class="info">
            <p>{{ item.username }}</p>
            <span class="iconfont icon-gengduo1"></span>
          </div>
        </router-link>
      </ul>

      <router-link
        tag="div"
        :to="'/videoplay?id=' + BigCardTopList[0].children[0].model.resource.id"
        class="allLook"
        v-if="BigCardTopList[0]"
      >
        <div class="img" v-if="videoLoading">
          <img
            :src="BigCardTopList[0].children[0].model.resource.cover"
            alt=""
          />
          <div class="imgBottom">
            <p>
              {{
                BigCardTopList[0].children[0].model.resource.count.count_view
                  | CountHandle
              }}
              观看 |
              {{
                BigCardTopList[0].children[0].model.resource.duration
                  | playerTime
              }}
            </p>
          </div>
        </div>
        <div class="video" v-else>
          <video
            src="../../video/fun.mp4"
            ref="video"
            @canplay="oncanplayFun"
            @timeupdate="ontimeupdateFun"
            muted
          ></video>
          <div class="process">
            <div class="line" :style="{ width: lineWidth }"></div>
          </div>
          <div class="iconfont icon-shengyinguan"></div>
          <div class="iconfont icon-shengyinkai icondisplay"></div>
        </div>
        <div
          class="tips"
          v-if="BigCardTopList[0].children[0].model.attributes.tags[0].title"
        >
          <span class="iconfont icon-zhiboguankanshu"></span
          >{{ BigCardTopList[0].children[0].model.attributes.tags[0].title }}
        </div>
        <div class="allLookInfo">
          <div class="left">
            <h3>
              {{ BigCardTopList[0].children[0].model.resource.title }}
            </h3>
            <div class="author">
              <div class="img">
                <img
                  :src="
                    BigCardTopList[0].children[0].model.resource.author.userinfo
                      .avatar
                  "
                  alt=""
                />
              </div>
              <p>
                {{
                  BigCardTopList[0].children[0].model.resource.author.userinfo
                    .username
                }}
              </p>
            </div>
          </div>
          <div class="right">
            <div class="shoucang">
              <div class="iconfont icon-shoucang"></div>
              <p>
                {{
                  BigCardTopList[0].children[0].model.resource.count
                    .count_collect
                }}
              </p>
            </div>
            <div class="dianzan">
              <div class="iconfont icon-dianzan1"></div>
              <p>
                {{
                  BigCardTopList[0].children[0].model.resource.count.count_like
                }}
              </p>
            </div>
            <span class="iconfont icon-gengduo1"></span>
          </div>
        </div>
      </router-link>

      <ul class="tuijian" v-if="MiddleCardList[1]">
        <router-link
          tag="li"
          :to="'/videoplay?id=' + item.id"
          v-for="item in MiddleCardList[1][0]"
          :key="item.id"
        >
