        </div>
        <ul class="authorUl" v-if="authorList">
          <li v-for="item in authorList" :key="item.model.resource.id">
            <div class="img">
              <img :src="item.model.resource.avatar" alt="" />
            </div>
            <h3 class="name">{{ item.model.resource.username }}</h3>
            <p class="count">
              {{ item.model.resource.count.count_article }} 作品 ·
              {{ item.model.resource.count.count_follower }} 粉丝
            </p>
            <p class="tips">{{ item.model.resource.occupation }}</p>
            <div class="follow">+ 关注</div>
          </li>
        </ul>
      </div>

      <ul class="tuijian" v-if="MiddleCardList[2]">
        <router-link
          tag="li"
          :to="'/videoplay?id=' + item.id"
          v-for="item in MiddleCardList[2][0]"
          :key="item.id"
        >
          <div class="img">
            <img :src="item.cover" alt="" />
            <div class="vedioInfo">
              <div class="left">
                <div class="watch">
                  <span class="iconfont icon-zhiboguankanshu"></span>
                  {{ item.count.count_view | CountHandle }}
                </div>
                <div class="like">
                  <span class="iconfont icon-dianzan1"></span>
                  {{ item.count.count_like }}
                </div>
              </div>
              <div class="time">{{ item.duration | playerTime }}</div>
            </div>
          </div>
          <h3>{{ item.title }}</h3>
          <div class="info">
            <p>{{ item.username }}</p>
            <span class="iconfont icon-gengduo1"></span>
          </div>
        </router-link>
      </ul>

      <ul class="tuijian" v-if="MiddleCardList[3]">
        <router-link
            tag="li"
            :to="'/videoplay?id=' + item.id"  v-for="item in MiddleCardList[3][0]" :key="item.id">
          <div class="img">
            <img :src="item.cover" alt="" />
            <div class="vedioInfo">
              <div class="left">
                <div class="watch">
                  <span class="iconfont icon-zhiboguankanshu"></span>
                  {{ item.count.count_view | CountHandle }}
                </div>
                <div class="like">
                  <span class="iconfont icon-dianzan1"></span>
                  {{ item.count.count_like }}
                </div>
              </div>
              <div class="time">{{ item.duration | playerTime }}</div>
            </div>
          </div>
          <h3>{{ item.title }}</h3>
          <div class="info">
            <p>{{ item.username }}</p>
            <span class="iconfont icon-gengduo1"></span>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import { getHomeRecommend } from "../../api/home";
export default {
  data() {
    return {
      current: 0,
      active: 0,
      durationTime: 1,
      currentTime: 0,
      lineWidth: "0",
      banners: null,
      MiddleCardList: [],
      BigCardTopList: [],
      videoLoading: true,
      authorList: [],
      advertisement: null,
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    oncanplayFun() {
      this.durationTime = this.$refs.video.duration;
    },
    ontimeupdateFun() {
      this.currentTime = this.$refs.video.currentTime;
      this.lineWidth = (this.currentTime / this.durationTime) * 100 + "%";
    },
    getHomeRecommendFun() {
      getHomeRecommend().then((msg) => {
        let data = msg.data;
        // console.log(data);
        // 轮播图
        let bannersIndex = data.children.findIndex(
          (item) => item.type == "uiBanner"
        );
        this.banners = data.children[bannersIndex].children;
        // 所有中型卡片
        let MiddleCard = [];
        data.children.forEach((item) => {
          if (item.type == "uiMiddleCard") {
            MiddleCard.push(item);
          }
        });
        let handleData = [];
        MiddleCard.forEach((item) => {
          handleData.push(item.children[0].model.resource);
        });
        // console.log(handleData);
        for (let i = 0; i < MiddleCard.length / 4; i++) {
          let arr = [];
          arr.push(handleData.slice(i * 4, i * 4 + 4));
          this.MiddleCardList.push(arr);
          // console.log(arr);
        }
        // console.log(this.MiddleCardList);

        // 所有大卡片
        data.children.forEach((item) => {
          if (item.type == "uiBigCardTop") {
            this.BigCardTopList.push(item);
          }
        });

        // 作者
        let uiCollectionSlideOneIndex = data.children.findIndex(
          (item) => item.type == "uiCollectionSlideOne"
        );
        this.authorList = data.children[uiCollectionSlideOneIndex].children;

        let advertisementIndex = data.children.findIndex(
          (item) => item.type == "uiBigCard"
        );
        this.advertisement =
          data.children[advertisementIndex].children[0].model.resource;
      });
    },
  },
  created() {
    this.getHomeRecommendFun();
  },

  filters: {
    CountHandle(val) {
      if (val < 10000) {
        return val;
      }
      let w = val > 10000 ? val / 10000 : val; //万计算
      return w.toFixed(1) + "w";
    },
    // 视频播放时间
    playerTime(data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "00'00''";
      } else {
        return (m < 10 ? "0" + m : m) + "'" + (s < 10 ? "0" + s : s) + "''";
      }
    },
  },
};
</script>

