import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/searchview',
    name: 'searchview',
    component: ()=>import("../views/SearchView")
  },
  {
    path: '/searchmsg',
    name: 'searchmsg',
    component: ()=>import("../views/SearchMsg")
  },
  {
    path: '/userinfo',
    name: 'userinfo',
    component: ()=>import("../views/UserInfo")
  },
  {
    path: '/myview',
    name: 'myview',
    component: ()=>import("../views/MyView")
  },
  {
    path: '/videoplay',
    name: 'videoplay',
    component: ()=>import("../views/VideoPlay")

  },
  {
    path: '/notesview',
    name: 'notesview',
    component: ()=>import("../views/NotesView")

  },
  {
    path: '/notesdetail',
    name: 'notesdetail',
    component: ()=>import("../views/NotesDetail")
  },
  {
    path: '/followview',
    name: 'followview',
    component: ()=>import("../views/FollowView")
  },
  {
    path: '/categoryview',
    name: 'categoryview',
    component: ()=>import("../views/CategoryView")
  },
  {
    path: '/loginview',
    name: 'loginview',
    component: ()=>import("../views/LoginView")
  },
  {
    path: '/registerview',
    name: 'registerview',
    component: ()=>import("../views/RegisterView")
  },
  {
    path: '/mylikeview',
    name: 'mylikeview',
    component: ()=>import("../views/MylikeView")
  },
  {
    path: '/mycollectview',
    name: 'mycollectview',
    component: ()=>import("../views/MycollectView")
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
