import request from "./http";

// 首页-推荐
export function getHomeRecommend(){
    return request({
        url:"/home/recommend",
        method:"get"
    })
}

// 首页-今日精选
export function getHomeSelection(){
    return request({
        url:"/home/selection",
        method:"get"
    })
}

// 首页-今日精选
export function getHomeHot(){
    return request({
        url:"home/hot",
        method:"get"
    })
}

/**
 * 验证验证码
 * @param {String} channel 手机号码
 * @param {String} cateName 分类名称
 * @param {String} page 页
 */
 export function getCateRecommend(params){
    return request({
        url:"/home/cateRecommend",
        method:"get",
        params
    })
}