import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Sticky,Tab,Tabs,Swipe,SwipeItem, Loading,ShareSheet,DropdownMenu,DropdownItem,List,ActionSheet,Slider,Toast,Dialog } from 'vant';

Vue.use(Sticky).use(Tab).use(Tabs).use(Swipe).use(SwipeItem).use(Loading).use(ShareSheet).use(DropdownMenu).use(DropdownItem).use(List).use(ActionSheet).use(Slider).use(Toast).use(Dialog);

Vue.config.productionTip = false


import Video from 'video.js'
import 'video.js/dist/video-js.css'
	
Vue.prototype.$video = Video


import VuevideoPlayer from 'vue-video-player';
Vue.use(VuevideoPlayer);


// swiper插件

import "swiper/css/swiper.min.css"

import "./assets/font/iconfont.css"


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
