<template>
  <div>
    <div class="loadingView" v-if="!selectedList">
      <div class="loading"></div>
      <van-loading type="spinner" size="20" vertical />
    </div>
    <div class="jingxuan" v-else>
      <h2 class="title">今日精选</h2>
      <router-link
        tag="div"
        :to="'/videoplay?id=' + item.children[0].model.resource.id"
        class="jingxuanInfo"
        v-for="item in selectedList"
        :key="item.children[0].model.resource.id"
      >
        <div class="top">
          <div class="userInfo">
            <div class="img">
              <img
                :src="item.children[0].model.resource.author.userinfo.avatar"
                alt=""
              />
            </div>
            <p>
              {{ item.children[0].model.resource.author.userinfo.username }}
            </p>
          </div>
          <span class="iconfont icon-gengduo1"></span>
        </div>
        <p class="text">{{ item.children[0].model.resource.title }}</p>
        <div class="img" v-if="videoLoading">
          <img :src="item.children[0].model.resource.cover" alt="" />
          <div class="imgBottom">
            <p>
              {{
                item.children[0].model.resource.count.count_view | CountHandle
              }}
              观看 |
              {{ item.children[0].model.resource.duration | playerTime }}
            </p>
          </div>
        </div>
        <div class="video" v-else>
          <video
            src="../../video/fun.mp4"
            ref="video"
            @canplay="oncanplayFun"
            @timeupdate="ontimeupdateFun"
            muted
          ></video>
          <div class="process">
            <div class="line" :style="{ width: lineWidth }"></div>
          </div>
          <div class="iconfont icon-shengyinguan"></div>
          <div class="iconfont icon-shengyinkai icondisplay"></div>
        </div>
        <div class="bottom">
          <div class="shoucang">
            <span class="iconfont icon-shoucang"></span>
            {{ item.children[0].model.resource.count.count_collect }}
          </div>
          <div class="pinglun">
            <span class="iconfont icon-pinglun1"></span>
            {{ item.children[0].model.resource.count.count_comment }}
          </div>
          <div class="dianzan">
            <span class="iconfont icon-dianzan1"></span>
            {{ item.children[0].model.resource.count.count_like }}
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { getHomeSelection } from "../../api/home";
export default {
  data() {
    return {
      current: 0,
      active: 0,
      durationTime: 1,
      currentTime: 0,
      lineWidth: "0",
      videoLoading: true,
      selectedList: null,
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    oncanplayFun() {
      this.durationTime = this.$refs.video.duration;
    },
    ontimeupdateFun() {
      this.currentTime = this.$refs.video.currentTime;
      this.lineWidth = (this.currentTime / this.durationTime) * 100 + "%";
    },
    getHomeSelectionFun() {
      getHomeSelection().then((data) => {
        // console.log(data);
        this.selectedList = data.data.children.filter(
          (item, index) => index != 0
        );
        // console.log(this.selectedList);
      });
    },
  },
  filters: {
    CountHandle(val) {
      if (val < 10000) {
        return val;
      }
      let w = val > 10000 ? val / 10000 : val; //万计算
      return w.toFixed(1) + "w";
    },
    playerTime(data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "00'00''";
      } else {
        return (m < 10 ? "0" + m : m) + "'" + (s < 10 ? "0" + s : s) + "''";
      }
    },
  },
  created() {
    this.getHomeSelectionFun();
  },
};
</script>

<style lang="less">
.jingxuan {
  h2.title {
    height: 44px;
    line-height: 44px;
    font-size: 18px;
    color: #000;
    padding-left: 12px;
    padding-top: 7px;
    font-weight: 600;
    font-family: "黑体";
  }
  .jingxuanInfo {
    width: 93.6%;
    background-color: #fff;
    margin: 0 auto;
    margin-bottom: 7px;
    border-radius: 5px;
    .top {
      height: 32px;
      padding-top: 17px;
      padding-left: 15px;
      padding-right: 11px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .userInfo {
        display: flex;
        justify-content: space-between;
        .img {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 32px;
            height: 32px;
          }
        }
        p {
          line-height: 32px;
          font-size: 12px;
          padding-left: 6px;
          color: #212121;
        }
      }
      .icon-gengduo1 {
        font-size: 13px;
        color: #8f8f8f;
      }
    }
    .text {
      height: 34px;
      line-height: 34px;
      font-size: 14px;
      padding-left: 16px;
    }
    .img {
      width: 100%;
      overflow: hidden;
      position: relative;
      img {
        width: 100%;
      }
      .imgBottom {
        position: absolute;
        right: 10px;
        bottom: 0;
        height: 28px;
        line-height: 28px;
        font-size: 12px;
        color: rgba(255, 255, 255, 0.9);
      }
    }
    .video {
      width: 100%;
      overflow: hidden;
      position: relative;
      video {
        width: 100%;
        height: 100%;
      }
      .process {
        width: 100%;
        height: 3px;
        position: absolute;
        left: 0;
        bottom: 3px;
        background-color: rgba(255, 255, 255, 0.6);
        .line {
          width: 50%;
          height: 2px;
          background-color: #f0472f;
        }
      }
      .iconfont {
        color: rgba(255, 255, 255, 0.9);
        font-size: 17px;
        font-weight: bold;
        position: absolute;
        left: 13px;
        bottom: 13px;
      }
      .icondisplay {
        display: none;
      }
    }
    .bottom {
      display: flex;
      div {
        flex: 1;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 13px;
        .iconfont {
          font-size: 15px;
        }
      }
    }
  }
}
.loadingView {
  .loading {
    width: 100%;
    height: 240px;
  }
}
</style>