<template>
  <div>
    <div class="loadingView" v-if="!banners">
      <div class="loading"></div>
      <van-loading type="spinner" size="20" vertical />
    </div>
    <div class="hot" v-else>
      <div class="banner" v-if="banners">
        <van-swipe @change="onChange" autoplay="3000">
          <van-swipe-item v-for="item in banners" :key="item.model.cover">
            <img :src="item.model.cover" alt="" />
            <div class="describe">{{ item.model.title }}</div>
          </van-swipe-item>
          <template #indicator>
            <div class="custom-indicator">
              <span>{{ current + 1 }}</span> / {{ banners.length }}
            </div>
          </template>
        </van-swipe>
      </div>
      <ul class="btns">
        <li>
          <div class="svg">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-ziliao"></use>
            </svg>
          </div>
          <p>周榜单</p>
        </li>
        <li>
          <div class="svg">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-huore"></use>
            </svg>
          </div>
          <p>分类热门</p>
        </li>
        <li>
          <div class="svg">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shoucang1"></use>
            </svg>
          </div>
          <p>分类精选</p>
        </li>
        <li>
          <div class="svg">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-fenlei"></use>
            </svg>
          </div>
          <p>场库精选</p>
        </li>
      </ul>
      <div v-if="hotList">
        <router-link
            tag="div"
            :to="'/videoplay?id=' + item.children[0].model.resource.id"
          class="remenInfo"
          v-for="item in hotList"
          :key="item.children[0].model.resource.id"
        >
          <div class="top">
            <div class="userInfo">
              <div class="img">
                <img
                  :src="item.children[0].model.resource.author.userinfo.avatar"
                  alt=""
                />
              </div>
              <p>
                {{ item.children[0].model.resource.author.userinfo.username }}
              </p>
            </div>
            <span class="iconfont icon-gengduo1"></span>
          </div>
          <p class="text">{{ item.children[0].model.resource.title }}</p>
          <div class="img" v-if="videoLoading">
            <img :src="item.children[0].model.resource.cover" alt="" />
            <div class="imgBottom">
              <p>
                {{
                  item.children[0].model.resource.count.count_view | CountHandle
                }}
                观看 |
                {{ item.children[0].model.resource.duration | playerTime }}
              </p>
            </div>
          </div>
          <div class="video" v-else>
            <video
              src="../../video/fun.mp4"
              ref="video"
              @canplay="oncanplayFun"
              @timeupdate="ontimeupdateFun"
              muted
            ></video>
            <div class="process">
              <div class="line" :style="{ width: lineWidth }"></div>
            </div>
            <div class="iconfont icon-shengyinguan"></div>
            <div class="iconfont icon-shengyinkai icondisplay"></div>
          </div>
          <div class="bottom">
            <div class="shoucang">
              <span class="iconfont icon-shoucang"></span>
              {{ item.children[0].model.resource.count.count_collect }}
            </div>
            <div class="pinglun">
              <span class="iconfont icon-pinglun1"></span>
              {{ item.children[0].model.resource.count.count_comment }}
            </div>
            <div class="dianzan">
              <span class="iconfont icon-dianzan1"></span>
              {{ item.children[0].model.resource.count.count_like }}
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { getHomeHot } from "../../api/home";
export default {
  data() {
    return {
      current: 0,
      active: 0,
      durationTime: 1,
      currentTime: 0,
      lineWidth: "0",
      banners: null,
      hotList: [],
      videoLoading: true,
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    oncanplayFun() {
      this.durationTime = this.$refs.video.duration;
    },
    ontimeupdateFun() {
      this.currentTime = this.$refs.video.currentTime;
      this.lineWidth = (this.currentTime / this.durationTime) * 100 + "%";
    },
    getHomeHotFun() {
      getHomeHot().then((data) => {
        // console.log(data);
        this.banners = data.data.children[0].children;
        this.hotList = data.data.children.filter(
          (item) => item.type == "uiBigCard"
        );
      });
    },
  },
  filters: {
    CountHandle(val) {
      if (val < 10000) {
        return val;
      }
      let w = val > 10000 ? val / 10000 : val; //万计算
      return w.toFixed(1) + "w";
    },
    playerTime(data) {
      let m = parseInt(data / 60);
      let s = parseInt(data % 60);
      if (m == 0 && s == 0) {
        return "00'00''";
      } else {
        return (m < 10 ? "0" + m : m) + "'" + (s < 10 ? "0" + s : s) + "''";
      }
    },
  },
  created() {
    this.getHomeHotFun();
  },
};
</script>

<style lang="less">
.hot {
  .banner {
    width: 93.6%;
    height: 197px;
    overflow: hidden;
    margin: 0 auto;
    margin-top: 8px;
    border-radius: 7px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .custom-indicator {
      position: absolute;
      right: 16px;
      bottom: 14px;
      font-size: 10px;
      color: #fff;
      span {
        font-size: 17px;
        font-weight: bold;
      }
    }
    .describe {
      width: 100%;
      height: 46px;
      line-height: 46px;
      font-size: 15px;
      padding-left: 15px;
      color: #fff;
      position: absolute;
      left: 0;
      bottom: 0;
      background-image: linear-gradient(
        to bottom,
        rgba(255, 0, 0, 0),
        rgba(0, 0, 0, 0.3)
      );
    }
  }
  ul.btns {
    display: flex;
    li {
      flex: 1;
      text-align: center;
      padding-top: 16px;
      padding-bottom: 12px;
      .svg {
        width: 40px;
        height: 40px;
        font-size: 40px;
        margin: 0 auto;
      }
      p {
        height: 26px;
        line-height: 26px;
        font-size: 12px;
      }
    }
  }
  .remenInfo {
    width: 93.6%;
    background-color: #fff;
    margin: 0 auto;
    margin-bottom: 7px;
    border-radius: 5px;
    .top {
      height: 32px;
      padding-top: 17px;
      padding-left: 15px;
      padding-right: 11px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .userInfo {
        display: flex;
        justify-content: space-between;
        .img {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 32px;
            height: 32px;
          }
        }
        p {
          line-height: 32px;
          font-size: 12px;
          padding-left: 6px;
          color: #212121;
        }
      }
      .icon-gengduo1 {
        font-size: 13px;
        color: #8f8f8f;
      }
    }
    .text {
      height: 34px;
      line-height: 34px;
      font-size: 14px;
      padding-left: 16px;
    }
    .img {
      width: 100%;
      overflow: hidden;
      position: relative;
      img {
        width: 100%;
      }
      .imgBottom {
        position: absolute;
        right: 10px;
        bottom: 0;
        height: 28px;
        line-height: 28px;
        font-size: 12px;
        color: rgba(255, 255, 255, 0.9);
      }
    }
    .video {
      width: 100%;
      overflow: hidden;
      position: relative;
      video {
        width: 100%;
        height: 100%;
      }
      .process {
        width: 100%;
        height: 3px;
        position: absolute;
        left: 0;
        bottom: 3px;
        background-color: rgba(255, 255, 255, 0.6);
        .line {
          width: 50%;
          height: 2px;
          background-color: #f0472f;
        }
      }
      .iconfont {
        color: rgba(255, 255, 255, 0.9);
        font-size: 17px;
        font-weight: bold;
        position: absolute;
        left: 13px;
        bottom: 13px;
      }
      .icondisplay {
        display: none;
      }
    }
    .bottom {
      display: flex;
      div {
        flex: 1;
        height: 40px;
        line-height: 40px;
        text-align: center;
        font-size: 13px;
        .iconfont {
          font-size: 15px;
        }
      }
    }
  }
}
.loadingView {
  .loading {
    width: 100%;
    height: 240px;
  }
}
</style>